
















































































































































































































import Vue from 'vue';
import Auth from '@/libs/auth';
import QuizGroupApi from '@/apis/QuizGroupApi';
import { QuizGroupModel, QuizGroupPreviewModel } from '@/apis/models/QuizGroupModel';
import BackButton from '@/components/BackButton.vue';
import AudioPlayBtn from '@/components/quiz_group/AudioPlayBtn.vue';
import UpdateQuizVideoModal from '@/components/quiz_group/UpdateQuizVideoModal.vue';
import checkedItems from '@/components/test_maker/CheckedItems.vue';
import DifficultyMark from '@/components/quiz_maker/DifficultyMark.vue';
import ScoreEditor from '@/components/quiz_maker/ScoreEditor.vue';
import QuizApi from '@/apis/QuizApi';
import StorageApi from '@/apis/StorageApi';

export default Vue.extend({
  components: {
    BackButton,
    AudioPlayBtn,
    UpdateQuizVideoModal,
    checkedItems,
    DifficultyMark,
    ScoreEditor,
  },
  data() {
    return {
      isLoading: false,
      quizGroup: null as QuizGroupModel | null,
      isAudioPlaying: false,
      theAudio: new Audio(),
      totalScore: 0 as number | undefined,
      scoreEditor: false,
      imgHidden: false,
      isVideoEditing: false,
      videoLinkSelection: [] as string[],
    };
  },
  computed: {
    workspaceId(): string {
      return this.$store.state.currentWorkspace.uuid;
    },
    currentQuizGroup(): QuizGroupPreviewModel {
      return this.$store.state.currentQuizGroup;
    },
    quizGroupId(): string {
      return this.$store.state.currentQuizGroup.uuid;
    },
    editPermission(): boolean {
      return this.$store.state.editPermission;
    },
  },
  async mounted() {
    Auth.loginCheck();
    this.$store.commit('updateLoading', true);
    try {
      this.quizGroup = await QuizGroupApi.retrieve(this.quizGroupId);
      console.log(this.quizGroup.videoUrlList);
      if (!this.quizGroup.videoUrlList) {
        this.quizGroup.videoUrlList = [];
        console.log('----undefine----');
      }
      this.videoLinkSelection = this.quizGroup.videoUrlList;
      console.log(this.quizGroup.videoUrlList);

      // 幫助區
      this.quizGroup.quizList.forEach((quiz) => {
        if (quiz.answerSet[0] === '') {
          quiz.answerSet = [];
        }
      });
      // ------
      this.totalScore = this.quizGroup.totalScore;
    } finally {
      this.$store.commit('updateLoading', false);
    }
  },
  methods: {
    fileClick(event: any) {
      const filesList = event.target.files;
      this.addVideoFile(filesList);
    },
    async addVideoFile(filesList: File[]) {
      if (filesList.length > 0) {
        const videoFile = filesList[0];
        console.log(videoFile.name);
        this.uploadVideo(videoFile, videoFile.name);
      }
    },
    async uploadVideo(file: File, filename: string) {
      this.isLoading = true;
      try {
        const url = await StorageApi.uploadVideo(file, filename);
        console.log(url);
        this.videoLinkSelection.push(url);
      } catch (e) {
        console.error(e);
        this.$notify({
          type: 'error',
          title: this.$t('錯誤').toString(),
          text: this.$t('無法讀取檔案').toString(),
        });
      } finally {
        this.isLoading = false;
      }
    },
    toQuizGroupMaker() {
      this.$router.push('/manager/quizgroup-maker');
    },
    calculateTotal() {
      let num = 0;
      if (this.quizGroup) {
        this.quizGroup.quizList.forEach(async (instance) => {
          num += Number(instance.score);
        });
      }
      this.totalScore = num;
    },
    async updateEditedScore() {
      this.$store.commit('updateLoading', true);
      this.scoreEditor = false;
      this.imgHidden = false;
      try {
        if (this.quizGroup) {
          const requests = [] as Promise<string>[];
          this.quizGroup.quizList.forEach(async (instance) => {
            const payload = {
              quizGroupId: this.quizGroupId,
              quizId: instance.uuid,
              score: instance.score,
            };
            const promise = QuizApi.setScore(payload);
            requests.push(promise);
          });
          await Promise.all(requests);
        }
      } finally {
        this.$store.commit('updateLoading', false);
      }
    },
    cencleEditedScore() {
      this.scoreEditor = false;
      this.imgHidden = false;
      window.location.reload();
    },
    handleUpdateVideo() {
      this.$modal.show('update-quiz-vedio-modal', {
        quizGroup: this.quizGroup,
      });
    },
    updateSelection(url: string) {
      this.videoLinkSelection.push(url);
    },
    async updateQuizVideo() {
      this.$store.commit('updateLoading', true);
      const payload = {
        videoUrlList: this.videoLinkSelection,
      };
      try {
        if (this.quizGroup) {
          this.quizGroup.videoUrlList = this.videoLinkSelection;
          console.log(this.quizGroup.videoUrlList);
        }

        await QuizGroupApi.update(this.quizGroupId, payload);
        console.log(this.quizGroupId);

        this.isVideoEditing = !this.isVideoEditing;
      } finally {
        this.$store.commit('updateLoading', false);
      }
    },
    async saveAsPdf() {
      this.$store.commit('updateLoading', true);
      try {
        if (this.quizGroup) {
          await QuizGroupApi.saveAsPdf(this.quizGroupId, this.quizGroup.metadata.name);
        }
      } finally {
        this.$store.commit('updateLoading', false);
      }
    },
    openLink(videoUrl: string) {
      window.open(videoUrl, '_blank');
    },
  },
});
